footer {
    background-color: #070707;
    font-size: 1,2rem;
    color: #fff;
    font-weight: 300;
}

.footer-content{
    margin: 0 auto;
    padding: 83px 0;
    display: flex;
    width: 70%;
}   

.footer-content__element {
    width: 33%;
    padding: 0 50px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.footer-content__element_contacts {
text-align: left;
padding-top: 72px;
}

.footer-title {
    text-align: left;
    padding-bottom: 24px;
}

.footer-text {
    padding-top: 24px;
}

.footer-separator hr {
    text-align: left;
}

.footer-content__element_about {
    text-align: center;
    display: flex;
    justify-content: center;
    align-item: center;
}
.footer-content__element_map {
    display: flex;
    justify-content: center;
    align-item: center;
}
.footer-map {
    width: 100%; 
}

.footer-social__items {
    display: flex;
    justify-content: center;
    width: 60%;
    margin: 0 auto;
}

.footer-social {
    background-color: #111;
}

.footer-social__items_item {
    padding: 14px;
    background-color: #111;
    
    border-radius: 50%;
    border: 1px solid #c9a74d;
    margin: 37px 5px;
    font-size: 18px; 
    align-text: center;
    width: 50px;
    height: 50px;
    text-align: center;
    cursor: pointer;    
}
.footer-social__items_item a {
    color: #c9a74d;
}

.footer-social__items_item:hover {
    background-color: #c9a74d;
    color: #111;
    border-color: #c9a74d;
    transition: all 0.2s ease-out;
}
