* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.app-wrapper {
  width: 100%;
  margin: 0 auto;
  color: #030303;
  font-family: "Quicksand", sans-serif;
}

.app-container {
  width: 80%;
  margin: 0 auto;
}

h1 {
  font-size: 4.5rem;
  letter-spacing: 0.5rem;
  font-weight: 300;
}

h2 {
  font-size: 2.2rem;
  font-weight: 400;
}

h3 {
  font-size: 1.4rem;
  font-weight: 400;
}

h4 {
  font-size: 1.5rem;
  font-weight: 600;
}

h5 {
  font-size: 1rem;
  font-weight: 400;
}

hr {
  width: 30%;
  height: 0.5px;
  color: #c9a74d;
  background: #c9a74d;
  text-align: center;
  margin: 0 auto;
  border: none;
}

@media (max-width: 1200px) {
} /* for desktops */
@media (max-width: 991px) {
  h1 {
    font-size: 3.5rem;
  }
} /* for laptops */
@media (min-width: 768px) and (max-width: 990px) {
  h1 {
    font-size: 2.5rem;
  }
} /* for large tablets */
